<script>
export default {
  data () {
    return {
      parentMsg: '',
      jobs: [],
      // totalJob: [],
      countOfPage: 3,
      currPage: 0,
      filter_name: '',
      selected: null,
      page:'notlanding',
      status: "",
      // requirementsource_url: "https://wetogetherjobdb.herokuapp.com/job?q=資料科學",
    }
  },
  created() {
    // window.addEventListener('load', () => {
    //   this.fetchData();// run after everything is in-place
    // })
    this.fetchData();// run after everything is in-place
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      // this.loading = true
      var _this = this;
      var jobURL = 'https://api.sheety.co/6a0215be-5e60-4fab-ba61-b57fbf900b60'
      // var jobURL = "https://wetogetherjobdb.herokuapp.com/job?updatedDate=3/11&_limit=100";
      fetch(jobURL)
      .then(function(response) {
        return response.json();
      })
      .then(function(myJson) {
        _this.jobs = myJson
        _this.currPage = 1;
        _this.$forceDate();
      });
      _this.loading = false;
    },
    setPage: function(idx){
      if (idx <= 0 || idx > this.totalPage) {
        return;
      } else {
        this.currPage = idx;
      }
    }
  },
  computed: {
    filteredRows: function(){
      var filter_name = this.filter_name.toLowerCase();
      return (this.filter_name.trim()!=='')?
        this.jobs.filter(function(d){return d.simpleJD.toLowerCase().reverse().indexOf(filter_name) > -1;}):
        this.jobs.reverse();
    },
    pageStart: function(){
      return (this.currPage - 1)* this.countOfPage;
    },
    totalPage: function(){
      return Math.ceil(this.filteredRows.length / this.countOfPage);
    },
    id(){
      return categothery[job.id];
    }
  },
}


